<template>

  <li class="colors__item" >
    <label class="colors__label">
      <input class="colors__radio sr-only" type="radio" name="color" :value="color"  @click="colorClick"  >
      <span class="colors__value"  :style="{ backgroundColor:color.code }" >
      </span>
    </label>
  </li>

</template>
<script>


export default {
  props: ['color'],
   watch:{
    color(value){
      this.color = value;
    },
  },
  methods: {
    colorClick(){
      this.$emit('update:currentColorId', this.color.id);
    },
},
}
</script>
