<template>
  <div class="form__counter">
  <button type="button" aria-label="Убрать один товар" @click="deleteCount">
    <svg width="12" height="12" fill="currentColor">
      <use xlink:href="#icon-minus"></use>
    </svg>
  </button>
    <input type="text" v-model.number="productAmount">
  <button type="button" aria-label="Добавить один товар" @click="addCount">
    <svg width="12" height="12" fill="currentColor">
      <use xlink:href="#icon-plus"></use>
    </svg>
  </button>
  </div>

</template>

<script>

import { mapActions } from 'vuex';

export default {
  props: ['amount', 'productId'],
  data() {
    return {
      productAmount: this.amount,
    };
  },
  methods: {
    ...mapActions(['updateCartProductAmount']),
    addCount(){
      this.updateCartProductAmount({ productId: this.productId, amount: this.productAmount += 1 });
    },
    deleteCount(){
      this.updateCartProductAmount({ productId: this.productId, amount: this.productAmount >= 2 ? this.productAmount -= 1 : this.productAmount });
    },
  },
};
</script>
