<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item" >
      <button class="pagination__link pagination__link--arrow "  :class="{'pagination__link--disabled': page === 1} " :disabled="page === 1"
         aria-label="Предыдущая страница"  @click.prevent="prevPage" >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </button>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a class="pagination__link" href="#" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>

    <li class="pagination__item">
      <button class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page >= pages }" :disabled="page >= pages"
         href="#" aria-label="Следующая страница"   @click.prevent="nextPage()" >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </button>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages(){
      return Math.ceil(this.count / this.perPage);
    }
  },
  methods: {
  paginate(page) {
    this.$emit('paginate', page);
  },
    nextPage(){
      this.page++;
      this.$emit('paginate', this.page);
    },
    prevPage(){
      this.page--;
      this.$emit('paginate', this.page);

    },
  },
}

</script>
